.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
}

.search-form {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
  display: flex;
}

.search-form input[type="text"] {
  width: 100%;
  border-radius: 25px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  border-radius: 25px;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  padding: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.photo-card .info {
  margin-top: 10px;
}

.photo-card .info-item {
  margin-bottom: 5px;
  font-size: 14px;
}

.button-container {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.load-more {
  width: 250px;
  border-radius: 25px;
}

